<template>
  <div class="standard">
    <crumb></crumb>
    <div class="container">
      <div class="problemTitle">
        <div class="icon">
          <div>？</div>
        </div>
        <div class="info">
          <div>
            <div class="title">工程教育认证标准</div>
          </div>
          <div class="date">2017年11月</div>
        </div>
      </div>
      <div class="content">
        <p class="firstLevel">说明</p>
        <p class="secondLevel">1. 本标准适用于普通高等学校本科工程教育认证。</p>
        <p class="secondLevel">2. 本标准由通用标准和专业补充标准组成。</p>
        <p class="secondLevel">3. 申请认证的专业应当提供足够的证据，证明该专业符合本标准要求。</p>
        <p class="secondLevel">4. 本标准在使用到以下术语时，其基本涵义是：</p>
        <p class="secondLevel">4. 本标准在使用到以下术语时，其基本涵义是：</p>
        <p class="thirdLevel">（1）培养目标：培养目标是对该专业毕业生在毕业后5 年左右能够达到的职业和专业成就的总体描述。</p>
        <p class="thirdLevel">（2）毕业要求：毕业要求是对学生毕业时应该掌握的知识和能力的具体描述，包括学生通过本专业学习所掌握的知识、技能和素养。</p>
        <p class="thirdLevel">（3）评估：指确定、收集和准备各类文件、数据和证据材料的工作，以便对课程教学、学生培养、毕业要求、培养目标等进行评价。有效的评估需要恰当使用直接的、间接的、量化的、非量化的手段,评估过程可以采用合理的抽样方法。</p>
        <p class="thirdLevel">（4）评价：评价是对评估过程中所收集到的资料和证据进行解释的过程，评价结果是提出相应改进措施的依据。</p>
        <p class="thirdLevel">（5）机制: 指针对特定目的而制定的一套规范的处理流程，包括目的、相关规定、责任人员、方法和流程等，对流程涉及的相关人员的角色和责任有明确的定义。</p>
        <p class="secondLevel">5. 本标准中所提到的“复杂工程问题”必须具备下述特征（1），同时具备下述特征（2）-（7）的部分或全部：</p>
        <p class="thirdLevel">（1）必须运用深入的工程原理，经过分析才可能得到解决；</p>
        <p class="thirdLevel">（2）涉及多方面的技术、工程和其它因素，并可能相互有一定冲突；</p>
        <p class="thirdLevel">（3）需要通过建立合适的抽象模型才能解决，在建模过程中需要体现出创造性；</p>
        <p class="thirdLevel">（4）不是仅靠常用方法就可以完全解决的；</p>
        <p class="thirdLevel">（5）问题中涉及的因素可能没有完全包含在专业工程实践的标准和规范中；</p>
        <p class="thirdLevel">（6）问题相关各方利益不完全一致；</p>
        <p class="thirdLevel">（7）具有较高的综合性，包含多个相互关联的子问题。</p>
        <p class="firstLevel">通用标准</p>
        <p class="secondLevel">1  学生</p>
        <p class="thirdLevel">1.1 具有吸引优秀生源的制度和措施。</p>
        <p class="thirdLevel">1.2 具有完善的学生学习指导、职业规划、就业指导、心理辅导等方面的措施并能够很好地执行落实。</p>
        <p class="thirdLevel">1.3 对学生在整个学习过程中的表现进行跟踪与评估，并通过形成性评价保证学生毕业时达到毕业要求。</p>
        <p class="thirdLevel">1.4 有明确的规定和相应认定过程，认可转专业、转学学生的原有学分。</p>
        <p class="secondLevel">2  培养目标</p>
        <p class="thirdLevel">2.1 有公开的、符合学校定位的、适应社会经济发展需要的培养目标。</p>
        <p class="thirdLevel">2.2 定期评价培养目标的合理性并根据评价结果对培养目标进行修订，评价与修订过程有行业或企业专家参与。</p>
        <p class="secondLevel">3  毕业要求</p>
        <p class="secondLevel">专业必须有明确、公开、可衡量的毕业要求, 毕业要求应能支撑培养目标的达成。专业制定的毕业要求应完全覆盖以下内容：</p>
        <p class="thirdLevel">3.1工程知识：能够将数学、自然科学、工程基础和专业知识用于解决复杂工程问题。</p>
        <p class="thirdLevel">3.2问题分析：能够应用数学、自然科学和工程科学的基本原理，识别、表达、并通过文献研究分析复杂工程问题，以获得有效结论。</p>
        <p class="thirdLevel">3.3设计/开发解决方案：能够设计针对复杂工程问题的解决方案，设计满足特定需求的系统、单元（部件）或工艺流程，并能够在设计环节中体现创新意识，考虑社会、健康、安全、法律、文化以及环境等因素。</p>
        <p class="thirdLevel">3.4研究：能够基于科学原理并采用科学方法对复杂工程问题进行研究，包括设计实验、分析与解释数据、并通过信息综合得到合理有效的结论。</p>
        <p class="thirdLevel">3.5使用现代工具：能够针对复杂工程问题，开发、选择与使用恰当的技术、资源、现代工程工具和信息技术工具，包括对复杂工程问题的预测与模拟，并能够理解其局限性。</p>
        <p class="thirdLevel">3.6工程与社会：能够基于工程相关背景知识进行合理分析，评价专业工程实践和复杂工程问题解决方案对社会、健康、安全、法律以及文化的影响，并理解应承担的责任。</p>
        <p class="thirdLevel">3.7环境和可持续发展：能够理解和评价针对复杂工程问题的工程实践对环境、社会可持续发展的影响。</p>
        <p class="thirdLevel">3.8职业规范：具有人文社会科学素养、社会责任感，能够在工程实践中理解并遵守工程职业道德和规范，履行责任。</p>
        <p class="thirdLevel">3.9个人和团队：能够在多学科背景下的团队中承担个体、团队成员以及负责人的角色。</p>
        <p class="thirdLevel">3.10沟通：能够就复杂工程问题与业界同行及社会公众进行有效沟通和交流，包括撰写报告和设计文稿、陈述发言、清晰表达或回应指令。并具备一定的国际视野，能够在跨文化背景下进行沟通和交流。</p>
        <p class="thirdLevel">3.11项目管理：理解并掌握工程管理原理与经济决策方法，并能在多学科环境中应用。</p>
        <p class="thirdLevel">3.12终身学习：具有自主学习和终身学习的意识，有不断学习和适应发展的能力。</p>
        <p class="secondLevel">4  持续改进</p>
        <p class="thirdLevel">4.1 建立教学过程质量监控机制，各主要教学环节有明确的质量要求，定期开展课程体系设置和课程质量评价。建立毕业要求达成情况评价机制，定期开展毕业要求达成情况评价。</p>
        <p class="thirdLevel">4.2 建立毕业生跟踪反馈机制以及有高等教育系统以外有关各方参与的社会评价机制，对培养目标的达成情况进行定期分析。</p>
        <p class="thirdLevel">4.3. 能证明评价的结果被用于专业的持续改进。</p>
        <p class="secondLevel">5  课程体系</p>
        <p class="secondLevel">课程设置能支持毕业要求的达成，课程体系设计有企业或行业专家参与。课程体系必须包括：</p>
        <p class="thirdLevel">5.1与本专业毕业要求相适应的数学与自然科学类课程（至少占总学分的15%）。</p>
        <p class="thirdLevel">5.2符合本专业毕业要求的工程基础类课程、专业基础类课程与专业类课程（至少占总学分的30%）。工程基础类课程和专业基础类课程能体现数学和自然科学在本专业应用能力培养，专业类课程能体现系统设计和实现能力的培养。</p>
        <p class="thirdLevel">5.3工程实践与毕业设计（论文）（至少占总学分的20%）。设置完善的实践教学体系，并与企业合作，开展实习、实训，培养学生的实践能力和创新能力。毕业设计（论文）选题要结合本专业的工程实际问题，培养学生的工程意识、协作精神以及综合应用所学知识解决实际问题的能力。对毕业设计（论文）的指导和考核有企业或行业专家参与。</p>
        <p class="thirdLevel">5.4人文社会科学类通识教育课程（至少占总学分的15%），使学生在从事工程设计时能够考虑经济、环境、法律、伦理等各种制约因素。</p>
        <p class="secondLevel">6  师资队伍</p>
        <p class="thirdLevel">6.1 教师数量能满足教学需要，结构合理，并有企业或行业专家作为兼职教师。</p>
        <p class="thirdLevel">6.2 教师具有足够的教学能力、专业水平、工程经验、沟通能力、职业发展能力，并且能够开展工程实践问题研究，参与学术交流。教师的工程背景应能满足专业教学的需要。</p>
        <p class="thirdLevel">6.3 教师有足够时间和精力投入到本科教学和学生指导中，并积极参与教学研究与改革。</p>
        <p class="thirdLevel">6.4 教师为学生提供指导、咨询、服务，并对学生职业生涯规划、职业从业教育有足够的指导。</p>
        <p class="thirdLevel">6.5 教师明确他们在教学质量提升过程中的责任，不断改进工作。</p>
        <p class="secondLevel">7  支持条件</p>
        <p class="thirdLevel">7.1 教室、实验室及设备在数量和功能上满足教学需要。有良好的管理、维护和更新机制，使得学生能够方便地使用。与企业合作共建实习和实训基地，在教学过程中为学生提供参与工程实践的平台。</p>
        <p class="thirdLevel">7.2 计算机、网络以及图书资料资源能够满足学生的学习以及教师的日常教学和科研所需。资源管理规范、共享程度高。</p>
        <p class="thirdLevel">7.3 教学经费有保证，总量能满足教学需要。</p>
        <p class="thirdLevel">7.4学校能够有效地支持教师队伍建设，吸引与稳定合格的教师，并支持教师本身的专业发展，包括对青年教师的指导和培养。</p>
        <p class="thirdLevel">7.5 学校能够提供达成毕业要求所必需的基础设施，包括为学生的实践活动、创新活动提供有效支持。</p>
        <p class="thirdLevel">7.6 学校的教学管理与服务规范，能有效地支持专业毕业要求的达成。</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'standard',
  data() {
    return {
      
    }
  },
  mounted() {
    console.log(this.$route.query.id)
  },
  methods: {
    goback() {
      this.$router.push({
        name: 'problem'
      })
    }
  }
}
</script>

<style lang="scss">
.standard {
  .container {
    background-color: #fff;
    border-radius: 10px;
    .problemTitle {
      overflow: hidden;
      width: 100%;
      cursor: pointer;
      .icon {
        float: left;
        padding: 25px 20px 0 20px;
        div {
          width: 40px;
          height: 40px;
          border-radius: 50%;
          background-color: #e5f7f3;
          color: #03b187;
          text-align: center;
          line-height: 40px;
          font-size: 20px;
        }
      }
      .info {
        float: left;
        padding: 20px 0;
        margin-right: 20px;
        width: calc(100% - 100px);
        border-bottom: 1px solid #f4f4f4;
        >div {
          overflow: hidden;
          &:first-of-type {
            margin-bottom: 10px;
            .title {
              font-size: 18px;
              float: left;
              color: #333;
              font-weight: 700;
            }
          }
        }
        .date {
          font-size: 12px;
          letter-spacing: 0.1em;
          color: #999;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
    }
    .content {
      padding: 10px 50px 50px 80px;
      letter-spacing: 0.1em;
      line-height: 2;
      font-size: 12px;
      color: #333;
      p {
        text-align:justify;
        color:rgba(50, 50, 50, 0.7);
        line-height:30px;
        letter-spacing: 0.1em;
      }
      .firstLevel {
        padding-left: 0;
        font-size:16px;
        font-weight: 700;
      }
      .secondLevel {
        padding-left: 2em;
        font-size:14px;
      }
      .thirdLevel {
        padding-left: 4em;
        font-size:14px;
      }
    }
  }
}
</style>


